import React from 'react'
import flycompany from './flyght-01.png'
import './flightitem.css'

const FlightItem = (props) => {
    return (
        <div className="flight-item grid">
            <div className="left-block__item">
                <div className="head-item">
                    <div className="way-item">
                        departure
                    </div>
                    <div className="logo-company">
                        <img src={flycompany} alt=""/>
                    </div>
                </div>
            </div>
            <div className="center-block__item">
                <div className="flying-info">
                    {props.date.toLocaleString('en', {weekday: 'short'})} {props.date.getMonth() + 1}/{props.date.getDate()} {props.departure} - {props.destination}
                </div>
                <div className="flight-line">
                    <input type="radio" className="radio"/>
                    <div className="flight-line__departure">
                        <b>Departure</b>
                        <span>12:12</span>
                    </div>
                    <div className="divider">
                    </div>
                    <div className="flight-line__arrival">
                        <b>Arrival</b>
                        <span>14:12</span>
                    </div>
                    <div className="divider">
                    </div>
                    <div className="flight-line__time">
                        <b>Time</b>
                        <span>2:00</span>
                    </div>
                </div>
                <div className="flight-line">
                    <input type="radio" className="radio"/>
                    <div className="flight-line__departure">
                        <b>Departure</b>
                        <span>12:12</span>
                    </div>
                    <div className="divider">
                    </div>
                    <div className="flight-line__arrival">
                        <b>Arrival</b>
                        <span>14:12</span>
                    </div>
                    <div className="divider">
                    </div>
                    <div className="flight-line__time">
                        <b>Time</b>
                        <span>2:00</span>
                    </div>
                </div>
                <div className="flight-line">
                    <input type="radio" className="radio"/>
                    <div className="flight-line__departure">
                        <b>Departure</b>
                        <span>12:12</span>
                    </div>
                    <div className="divider">
                    </div>
                    <div className="flight-line__arrival">
                        <b>Arrival</b>
                        <span>14:12</span>
                    </div>
                    <div className="divider">
                    </div>
                    <div className="flight-line__time">
                        <b>Time</b>
                        <span>2:00</span>
                    </div>
                </div>
            </div>
            <div className="right-block__item">
                <div className="item-price">
                    321$
                </div>
                <button className="select-now">
                    select now
                </button>
            </div>
        </div>
    )
}

export default FlightItem